import { NgModule } from '@angular/core';
 
import { SharedModule } from '../shared/shared.module';
 
import { AddressModule } from '../address/address.module';
import { QuotesModule } from '../quotes/quotes.module';

import { ButtonsModule } from '@progress/kendo-angular-buttons';
import { InputsModule } from '@progress/kendo-angular-inputs';
import { DateInputsModule } from '@progress/kendo-angular-dateinputs';
import { DropDownsModule } from '@progress/kendo-angular-dropdowns';
import { GridModule, ExcelModule } from '@progress/kendo-angular-grid';

import { ProjectsRoutingModule } from './projects-routing.module'

import {
    DeleteProjectsPopupComponent,
    ExportProjectsPopupComponent,
    ProjectComponent,
    ProjectEditComponent,
    ProjectGridComponent,
    ProjectInternalComponent,
    ProjectPipelineNotesUpdateComponent,
    ProjectQuotesComponent,
    ProjectListComponent,
    TransferProjectPopupComponent,
    ProjectResolver,
    ProjectQuotesResolver,
    ProjectService
} from './index';

export const PROJECTS_COMPONENTS = [
    DeleteProjectsPopupComponent,
    ExportProjectsPopupComponent,
    ProjectComponent,
    ProjectEditComponent,
    ProjectGridComponent,
    ProjectInternalComponent,
    ProjectPipelineNotesUpdateComponent,
    ProjectQuotesComponent,
    ProjectListComponent,
    TransferProjectPopupComponent
] 

@NgModule({
    imports: [
        SharedModule.forRoot(),
    
        AddressModule,
        QuotesModule,
        InputsModule,
        DateInputsModule,
        DropDownsModule,
        GridModule,
        ButtonsModule,
        ProjectsRoutingModule
    ],
    exports: PROJECTS_COMPONENTS,
    declarations: PROJECTS_COMPONENTS,
    providers: [
        ProjectResolver,
        ProjectQuotesResolver,
        ProjectService
    ]
})
export class ProjectsModule { }



