import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import {
    ProjectEditComponent,
    ProjectComponent,
    ProjectListComponent,
} from './index';

import { CurrentUserResolver } from '../shared/services/common/user-resolver.service';
import { ProjectResolver, ProjectQuotesResolver } from './services/project-resolver.service';

const projectRoutes: Routes = [
    {
        path: 'projectCreate', 
        component: ProjectEditComponent,
        resolve: {
            projectModel: ProjectResolver,
            currentUser: CurrentUserResolver
        }
    },
    {
        path: 'projectEdit/:id',
        component: ProjectEditComponent,
        resolve: {
            projectModel: ProjectResolver,
            currentUser: CurrentUserResolver
        }
    },
    {
        path: 'project/:id',
        component: ProjectComponent,
        resolve: {
            currentUser: CurrentUserResolver,
            projectModel: ProjectResolver,
            projectQuotesModel: ProjectQuotesResolver

        }
    },
    {
        path: 'projectQuotes/:id',
        component: ProjectComponent,
        resolve: {
            projectModel: ProjectResolver,
            projectQuotesModel: ProjectQuotesResolver,
            currentUser: CurrentUserResolver
        }
    },
    {
        path: 'projectList',
        component: ProjectListComponent,
        resolve: {
            currentUser: CurrentUserResolver
        }
    }
];

@NgModule({
    imports: [RouterModule.forChild(projectRoutes)],
    exports: [RouterModule],
})
export class ProjectsRoutingModule {
}




